.frontpage-itemlist{
    background-color: lightblue;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}
.frontpage-item{
    
    padding: 9px 2%;
    width: 20%;
    display: flex;
    flex-direction: column;
}
.frontpage-item h3{
    width: 100%;
    height: 50px;
    flex:1;
}

.frontpage-item img{
    margin: 10px;
    flex:9;
    width: 90%;
    opacity: 0.8;
}